/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import {
  CalciteButton,
  CalciteDropdown,
  CalciteDropdownGroup,
  CalciteDropdownItem,
  CalciteIcon,
  CalciteModal,
} from '@esri/calcite-components-react';
import React, { useContext, useEffect, useRef, useState } from 'react';
import ThemeContext from '../ThemeContext';
import './Header.scss';
export const Header = (props: any) => {
  const ref = useRef<HTMLCalciteDropdownElement>(null);
  const disclaimer = useRef<HTMLCalciteModalElement>(null);

  const [links, setLinks] = useState<any>();
  const { theme, setTheme } = useContext(ThemeContext);
  useEffect(() => {
    fetch('./config.json').then((response) => {
      response.json().then((config) => {
        setLinks(config.links);
      });
    });
  }, []);
  return (
    <div>
      <header>
        <a
          href="https://www.wakegov.com/departments-government/geographic-information-services-gis/maps-apps-data/imaps-information"
          target="_blank"
          rel="noreferrer"
        >
          <img className="logo" src={theme === 'light' ? 'logo.svg' : 'logo_dark.svg'} />
        </a>
        <CalciteDropdown
          ref={ref}
          placement="top-end"
          scale="m"
          width="m"
          type="click"
          onCalciteDropdownSelect={(event: any) => {
            requestAnimationFrame(() => {
              const theme = event.target.querySelector('calcite-dropdown-item[active]')?.getAttribute('title');
              if (theme) {
                setTheme(theme);
                document.body.classList.remove(theme === 'light' ? 'dark' : 'light');
                document.body.classList.add(theme === 'light' ? 'light' : 'dark');

                const link = Array.from(document.head.querySelectorAll('link')).find((link: HTMLLinkElement) => {
                  return link.href.includes(`${theme}/main.css`);
                });
                if (link) {
                  document.head.appendChild(link);
                }
                window.localStorage.setItem('imaps_theme', theme);
              }
            });
          }}
        >
          <CalciteButton scale="s" slot="dropdown-trigger">
            <CalciteIcon icon="hamburger"></CalciteIcon>
          </CalciteButton>
          <CalciteDropdownGroup selection-mode="none" group-title="About" key="disclaimer">
            <CalciteDropdownItem
              rel="noreferrer"
              onClick={() => {
                if (disclaimer.current) {
                  disclaimer.current.active = true;
                }
              }}
            >
              Disclaimer
            </CalciteDropdownItem>
          </CalciteDropdownGroup>
          {links &&
            links.map((group: any) => {
              return (
                <CalciteDropdownGroup selection-mode="none" group-title={group.title} key={group.title}>
                  {group.links.map((link: any) => {
                    return (
                      <CalciteDropdownItem rel="noreferrer" href={link.href} target="_blank" key={link.title}>
                        {link.title}
                      </CalciteDropdownItem>
                    );
                  })}
                </CalciteDropdownGroup>
              );
            })}
          <CalciteDropdownGroup selection-mode="single" group-title="Theme">
            <CalciteDropdownItem title="light" active={theme === 'light' ? true : undefined}>
              Light
            </CalciteDropdownItem>
            <CalciteDropdownItem title="dark" active={theme === 'dark' ? true : undefined}>
              Dark
            </CalciteDropdownItem>
          </CalciteDropdownGroup>
        </CalciteDropdown>
      </header>

      <CalciteModal ref={disclaimer} aria-labelledby="modal-title">
        <div slot="header" id="modal-title">
          Disclaimer
        </div>
        <div slot="content">
          iMAPS makes every effort to produce and publish the most current and accurate information possible. However,
          the maps are productions for information purposed, and are NOT surveys. No warranties, expressed or implied,
          are provided for the data therein, its use, or its interpretation. Register of Deeds documents accessed
          through this site are unofficial. The official records are maintained at the Wake County Register of Deeds
          office. The Wake County Register of Deeds assumes no responsibility or liability associated with the use or
          misused of this data.
        </div>
        <CalciteButton
          slot="primary"
          width="full"
          onClick={() => {
            if (disclaimer.current) {
              disclaimer.current.active = false;
            }
          }}
        >
          Close
        </CalciteButton>
      </CalciteModal>
    </div>
  );
};
export default Header;
